@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body{font-size:14px;padding:0;margin:0px;background:linear-gradient(90deg, #020024 0%, #282e45 30%, #282e45 79%, #037d95 100%);color:white;font-family:"Roboto", sans-serif}*{box-sizing:border-box;scroll-behavior:smooth}a{outline:none;text-decoration:none;color:inherit}button{outline:none}.main-wrapper{width:100%;max-width:1200px;margin:0 auto;padding:0 10px}

header{width:100%;padding:20px 20px;position:relative}header div#header{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}header div#header h3#logo{font-size:2em}header div#header div#input-div{position:relative}header div#header div#input-div input{padding:10px 10px;border:none;outline:none;width:300px;border-radius:10px;transition:0.4s}header div#header div#input-div input:focus{box-shadow:0px 0px 10px 2px #7154AF}header div#header div#input-div ul#searchList{background-color:#282E45;position:absolute;overflow-y:scroll;overflow-x:hidden;height:150px;width:300px;padding:10px 10px;z-index:10000}header div#header div#input-div ul#searchList::-webkit-scrollbar{width:12px}header div#header div#input-div ul#searchList::-webkit-scrollbar-track{box-shadow:inset 0 0 1px white;border-radius:10px}header div#header div#input-div ul#searchList::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 3px white}header div#header div#input-div ul#searchList li{position:relative;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:5px;-webkit-align-items:center;align-items:center;padding:5px 5px;border-radius:5px}header div#header div#input-div ul#searchList li:hover{background-color:white;color:#282E45;font-weight:bold}header div#header div#input-div ul#searchList li span{width:70%;cursor:default}header div#header div#input-div ul#searchList li img{height:16px;cursor:pointer}header div#header div#input-div ul#searchList img.loader{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:100px;overflow:hidden}div.half{width:50%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-width:300px}@media screen and (max-width: 900px){div.half{-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}div.halfdiv.half:last-child{position:right}header div#header h3#logo{font-size:1.6em}}@media screen and (max-width: 600px){header div#header div#input-div input{padding:5px 10px;width:280px}h3#logo{margin-bottom:20px}div.half{-webkit-flex-direction:column;flex-direction:column}}@media screen and (max-width: 900px){div.half{-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}div.halfdiv.half:last-child{position:right}header div#header h3#logo{font-size:1.6em}}

div#toWatchList h1{font-size:2.5em;margin:30px 0;font-weight:bold}div.toWatchMovie{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:20px 20px;-webkit-align-items:flex-end;align-items:flex-end;border-bottom:0.2px solid #aaa}div.toWatchMovie .content{display:-webkit-flex;display:flex}div.toWatchMovie .content span.rating{font-size:2.5em;border:2px solid #941FE8;border-radius:100%;padding:10px;width:70px;height:70px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;cursor:default;margin-right:20px}div.toWatchMovie .content div.movieInfo{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around}div.toWatchMovie .content div.movieInfo h2{font-size:1.5em;color:#7820CD;font-weight:bold;cursor:pointer}div.toWatchMovie .content div.movieInfo span.genre{font-size:1.2em;cursor:pointer}div.toWatchMovie .content div.movieInfo span.genre:hover{color:#7820CD;text-decoration:underline}div.toWatchMovie div.icons{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;width:70px}div.toWatchMovie div.icons img{margin-left:20px;cursor:pointer}div.title-selector{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}@media screen and (max-width: 600px){div.title-selector{-webkit-justify-content:center;justify-content:center}div.content{-webkit-flex-direction:column;flex-direction:column}div#toWatchList h1{font-size:2.5em}div.toWatchMovie{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start}div.toWatchMovie .content span.rating{font-size:1.5em;padding:5px;width:55px;height:55px;margin-bottom:15px}div.toWatchMovie .content div.movieInfo h2{font-size:1.3em;margin-bottom:15px}div.icons{margin-top:10px}}

div#movie-details{display:-webkit-flex;display:flex;height:80vh;-webkit-align-items:center;align-items:center;width:100%;padding:30px 0;margin:0 auto}div#movie-details div.poster{width:50%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}div#movie-details div.poster img{border:2px solid #7154AF;border-radius:15px;transition:0.4s}div#movie-details div.poster img:hover{box-shadow:0px 0px 10px 2px #7154AF}div#movie-details div.details{width:50%;position:relative}div#movie-details div.details span.rating-big{position:absolute;right:0;font-size:1.6em;border:2px solid #941fe8;border:2px solid;border-radius:100%;padding:10px;width:50px;height:50px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;cursor:default}div#movie-details div.details h1{font-size:2em;margin-bottom:20px;color:#941fe8}div#movie-details div.details h1 span{color:white}div#movie-details div.details h3{font-size:1.5em;margin-bottom:15px;line-height:1.2em;color:#941fe8}div#movie-details div.details h3 span{color:white}div#movie-details div.details h3 div.plot{margin-top:15px}div#movie-details div.details h3.actors{display:-webkit-flex;display:flex}div#movie-details div.details h3:last-child{margin-top:1.5em}@media screen and (max-width: 720px){header{padding:30px 30px}div#movie-details{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:90%}div#movie-details div.details{width:100%;padding:30px 5px;margin-left:30px}div#movie-details div.details span.rating-big{position:relative;margin-bottom:15px}div#movie-details div.details div#movie-details div.details h1{width:50%;font-size:1.2em;margin-bottom:10px}div#movie-details div.details h3{font-size:1.2em;margin-bottom:10px}div#movie-details div.poster{width:250px;-webkit-justify-content:center;justify-content:center}}

div.filter{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:120px;-webkit-flex-wrap:wrap;flex-wrap:wrap;opacity:0.99}div.filter h3{font-size:1.3em;font-weight:bold}div.filter select,div.filter button{width:150px;border-radius:15px;color:#7154AF;font-size:1.2em;background-color:transparent;outline:none;padding:10px 20px;height:50px;cursor:pointer;-webkit-appearance:none;transition:0.3s}div.filter select:hover,div.filter button:hover{box-shadow:0 0 10px white}div.filter select{width:150px}div.filter option{background-color:#282E45}@media screen and (max-width: 600px){div.filter div.filter{-webkit-flex-direction:column;flex-direction:column}}

