div#toWatchList {
    h1 {
      font-size: 2.5em;
      margin: 30px 0;
      font-weight: bold;
    }
  }
  
  div.toWatchMovie {
    display: flex;
    justify-content: space-between;
    padding: 20px 20px;
    align-items: flex-end;
    border-bottom: 0.2px solid #aaa;
  
    .content {
      display: flex;
  
      span.rating {
        font-size: 2.5em;
        border: 2px solid #941FE8;
        border-radius: 100%;
        padding: 10px;
        width: 70px;
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: default;
        margin-right: 20px;
      }
  
      div.movieInfo {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
  
        h2 {
          font-size: 1.5em;
          color:#7820CD ;
          font-weight: bold;
          cursor: pointer;
        }
        span.genre {
          font-size: 1.2em;
          cursor: pointer;
  
          &:hover {
            color: #7820CD;
            text-decoration: underline;
          }
        }
      }
    }
    div.icons {
      display: flex;
      justify-content: flex-end;
  
      width: 70px;
      img {
        margin-left: 20px;
        cursor: pointer;
      }
    }
  }
  
  div.title-selector {
    display: flex;
    justify-content: space-between;
  }
  
  @media screen and (max-width: 600px) {
    div.title-selector {
      justify-content: center;
    }
    div.content {
      flex-direction: column;
    }
    div#toWatchList h1 {
      font-size: 2.5em;
    }
  
    div.toWatchMovie {
      flex-direction: column;
      align-items: flex-start;
    }
  
    div.toWatchMovie .content span.rating {
      font-size: 1.5em;
      padding: 5px;
      width: 55px;
      height: 55px;
      margin-bottom: 15px;
    }
    div.toWatchMovie .content div.movieInfo h2 {
      font-size: 1.3em;
      margin-bottom: 15px;
    }
    div.icons {
      margin-top: 10px;
    }
  }
  